.testimonials {
    min-width: 960px;
    background-color: #1C0028;
}

.testimonials .container {
    width: 100%;
    max-width: 960px;  /* Use max-width to limit the container size */
    margin: 0 auto;
    padding: 4rem 1rem;  /* Adjust padding for responsiveness */
}

.testimonials h2 {
    font-size: 2rem;
    text-align: center;
    color: #ffffff;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.testimonials .container .content {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));  /* Responsive grid layout */
    grid-gap: 20px;
    margin: 4rem 0;
}

.testimonials .card {
    border: 1px solid #6E88FF;
    background-color: #6E88FF;
    border-radius: 8px;
    box-shadow: 0 3px 10px rgb(0, 0, 0, .2);
    padding: 1rem;
}

.testimonials img {
    border-radius: 50px;
    margin-top: -2rem;
}

.testimonials .card p {
    color: #ffffff;
    font-size: 1.1rem;
    margin: 12px;
}

.testimonials .card span {
    color: #6E88FF;
    font-size: 1rem;
    font-weight: 700;
}

@media screen and (max-width: 100%) {
    .testimonials .container {
        width: 100%;
    }

    .testimonials .container .content {
        grid-template-columns: 1fr;
    }

    .testimonials .card {
        max-width: 100%;
        margin: 1rem;
    }
}

















