@keyframes gradientAnimation {
    0% {
        background-position: 100% 50%;
    }
    50% {
        background-position: 0% 50%;
    }
    100% {
        background-position: 100% 50%;
    }
}

.hero {
    color: #1C0028;
    height: 100vh;
    min-width: 960px;
    position: relative;
    background: linear-gradient(45deg, #FF0CAC, #FDE1EF, #FF0CAC);
    background-size: 300% 100%;
    animation: gradientAnimation 8s linear infinite;
    display: flex;
}

.hero .content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
    padding: 8rem;
}

.hero .content p:first-child {
    font-size: 30px;
    font-family: 'TheBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
}

.hero .content p:nth-child(2) {
    font-size: 50px;
    font-weight: 800;
    font-family: 'Pointless', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.hero .content p:nth-child(3) {
    font-size: 30px;
    font-family: 'TheBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    font-weight: bolder;
    padding-top: 3%;
}

.hero .button{
    margin: 1rem 0;
    width: 300px;
    border-radius: 40px;
    background: #FDE1EF;
    color: #1C0028;
}

.hero .button:hover {
    background-color: #1C0028;
    border: 1px solid var(--secondary-dark);
    color: #FDE1EF;
}

.hero .image {
    max-width: 40%;
    margin-left: auto;
    margin-right: 40px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 70px;
}

.hero .image img {
    max-width: 100%;
    height: auto;
    border-radius: 8px;
}

.hero .image .button.schedule-button {
    margin: 1rem;
    width: 100px;
    border-radius: 40px;
    background: #FDE1EF;
    color: #333;
}

.hero .image .schedule-button:hover {
    background-color: #1C0028;
    border: 1px solid var(#1C0028);
}

/* Media Queries for Responsive Design */

@media screen and (max-width: 767px) {
    .hero .content {
        padding: 4rem;
    }

    .hero .content p:first-child {
        font-size: 25px;
    }

    .hero .content p:nth-child(2) {
        font-size: 40px;
    }

    .hero .content p:nth-child(3) {
        font-size: 20px;
    }

    .hero .button {
        width: 200px;
    }

    .hero .image {
        max-width: 100%;
        margin: 0;
        padding: 20px;
        justify-content: center;
    }
}

